'use client';
import { useState, useEffect } from 'react';
import MapWithRouting from './MapWithRouting';
import fetchGoogleSheet from '@/lib/fetchGoogleSheet';

export default function StoreLocator() {
  const [locations, setLocations] = useState([]);
  const [selected, setSelected] = useState(0);

  useEffect(() => {
    async function loadLocations() {
      const data = await fetchGoogleSheet();
      setLocations(data);
    }
    loadLocations();
  }, []);

  if (!locations.length) return <p>Loading locations...</p>;

  return (
    <section className="max-w-6xl mx-auto p-6">
      <h2 className="text-3xl font-bold mb-4">Find Our Offices</h2>

      <div className="flex gap-4 overflow-x-auto pb-3 mb-4">
        {locations.map((loc, index) => (
          <button
            key={index}
            onClick={() => setSelected(index)}
            className={`px-6 py-2 rounded-full border shrink-0 transition ${
              selected === index
                ? "bg-black text-white"
                : "bg-white text-gray-600 border-gray-400"
            }`}
          >
            {loc.name}
          </button>
        ))}
      </div>

      <MapWithRouting location={locations[selected]} />
    </section>
  );
}
